.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Mine */
body,
html {
    height: 100%;
}

body {
    margin: 0;
    justify-content: center;
    align-items: center;
}

.container {
    height: 100%;
}

#welcome {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

#homeBtns {
    display: flex;
    flex-direction: row;
}

#activeModel {
    display: inline-block
}

#saveBtn {
    display: none;
}

#trainStats {
    width: 60%;
    margin: auto;
}

#badEntriesBar, #goodEntriesBar {
    width: 0%;
}

#carCanvas {
    background: black;
}

#networkCanvas {
    background: black;
}